// @flow

import React, { Component } from 'react';
import { Route } from 'react-router';

import About from './Pages/AboutPage/about';
import Work from './Pages/WorkPages/work';
import Contact from './Pages/ContactPage/contact';
import Post from './Pages/PostPage/post';
import Home from './Pages/HomePage/home';

import Navigation from '../components/navigation';
import Footer from '../components/footer';

class AppWrapper extends Component {
  componentDidMount() {
    this._colorAppBackground();
  }

  componentDidUpdate() {
    this._colorAppBackground();
  }

  render() {
    return (
      <div className="appGrid">
        <Navigation />
        <Route exact path="/" component={Home} />
        <Route path="/about" component={About} />
        <Route path="/work" component={Work} />
        <Route path="/contact" component={Contact} />
        <Route path="/post/:id" component={Post} />
        <Footer />
      </div>
    );
  }

  _colorAppBackground() {
    switch (true) {
      case this.props.location.pathname.includes('/about'):
        document
          .getElementsByClassName('appGrid')[0]
          .classList.remove('default', 'work', 'contact', 'post');
        document.getElementsByClassName('appGrid')[0].classList.add('about');
        break;
      case this.props.location.pathname.includes('/work'):
        document
          .getElementsByClassName('appGrid')[0]
          .classList.remove('default', 'about', 'contact', 'post');
        document.getElementsByClassName('appGrid')[0].classList.add('work');
        break;
      case this.props.location.pathname.includes('/contact'):
        document
          .getElementsByClassName('appGrid')[0]
          .classList.remove('default', 'about', 'work', 'post');
        document.getElementsByClassName('appGrid')[0].classList.add('contact');
        break;
      case this.props.location.pathname.includes('/post'):
        document
          .getElementsByClassName('appGrid')[0]
          .classList.remove('default', 'about', 'work', 'contact');
        document.getElementsByClassName('appGrid')[0].classList.add('post');
        break;
      default:
        document
          .getElementsByClassName('appGrid')[0]
          .classList.remove('about', 'work', 'contact', 'post');
        document.getElementsByClassName('appGrid')[0].classList.add('default');
        break;
    }
  }
}

export default AppWrapper;
