// @flow

import React from 'react';

import {
  ContactMain,
  ContactForm,
  FormSubmitLabel,
  FormSubmitSubmit
} from '../../../assets/styles/styles';
import { Helmet } from 'react-helmet';
import InputGroup from '../../../components/Input-group';
import PageHeader from '../../../components/page-header';

const fieldData = [
  {
    label: 'name',
    placeholder: 'full name',
    type: 'text'
  },
  {
    label: 'email',
    placeholder: 'email address',
    type: 'email'
  },
  {
    label: 'message',
    placeholder: 'Type you message here...',
    type: 'textarea'
  }
];

const Contact = () => {
  const inputGroupList = fieldData.map(field => (
    <InputGroup
      key={field.label}
      label={field.label}
      placeholder={field.placeholder}
      type={field.type}
    />
  ));

  return (
    <ContactMain>
      <Helmet>
        <title>Elliot Evans - Contact</title>
        <meta name="description" content="Contact Page" />
        <meta
          name="keywords"
          content="Front End Developer, Web Application Developer, Web Developer, Javascript Developer"
        />
      </Helmet>
      <PageHeader
        heading={'Contact'}
        subHeading={'If you wish to contact me please fill out the form'}
      />
      <ContactForm
        action="https://formspree.io/ell15evans.nuls@googlemail.com"
        method="POST"
        id="sendForm"
      >
        {inputGroupList}
        <FormSubmitLabel>
          <FormSubmitSubmit type="submit" />
        </FormSubmitLabel>
      </ContactForm>
    </ContactMain>
  );
};

export default Contact;
