// @flow

import React from 'react';
import { connect } from 'react-redux';

import { getSelectedProject } from '../selectors';
import BlurImages from './blur-images';
import {
  MainProjectImage,
  MainProjectImageWrapper,
  SelectedProjectContainer
} from '../assets/styles/styles';

const mapStateToProps = state => ({
  project: getSelectedProject(state)
});

const SelectedProject = ({ project }) => (
  <SelectedProjectContainer>
    <MainProjectImageWrapper>
      <MainProjectImage src={project[0].main_image} alt="" />
    </MainProjectImageWrapper>

    <BlurImages
      title={project[0].title}
      description={project[0].long_description}
    />
  </SelectedProjectContainer>
);

const Project = connect(mapStateToProps)(SelectedProject);

export default Project;
