// @flow

import React, { Component } from 'react';

import { Helmet } from 'react-helmet';

import { Main, EECite } from '../../../assets/styles/styles';

import marked from 'marked';
import * as yamlFront from 'yaml-front-matter';

import BlogList from '../../../components/blog-list';
import Banner from '../../../components/banner';
import Card from '../../../components/card';

class Blog extends Component {
  state = {
    markdown: [],
    text: [
      'Elliot Evans.',
      'Front-end Developer...',
      'Example',
      'There would be a short description from one of my blog posts but instead here is some placeholder text'
    ]
  };

  static transformMarkdownContent(text) {
    const content = yamlFront.loadFront(text);

    return marked(content.__content);
  }

  static objectToArray(text) {
    const object = yamlFront.loadFront(text);

    return [
      object.date.toString(),
      object.path.toString(),
      object.title.toString(),
      object.introduction.toString()
    ];
  }

  componentDidMount() {
    const test = this._mapFiles(
      require.context('./../../../assets/posts', true, /\.md$/)
    );
    const markdownFiles = [];

    for (const key in test) {
      if (!test.hasOwnProperty(key)) continue;
      markdownFiles.push(test[key]);
    }

    this._loadMarkdownData(markdownFiles);
  }

  render() {
    if (this.state.markdown.length === 0) {
      return (
        <Main>
          <Helmet>
            <title>Elliot Evans - Home</title>
            <meta name="description" content="Home Page" />
            <meta
              name="keywords"
              content="Front End Developer, Web Application Developer, Web Developer, Javascript Developer"
            />
          </Helmet>
          <Banner text={this.state.text} />
          <Card text={this.state.text} />
          <EECite>Time and stuff</EECite>
        </Main>
      );
    } else {
      return (
        <Main>
          <Helmet>
            <title>Elliot Evans - Home</title>
            <meta name="description" content="Home Page" />
            <meta
              name="keywords"
              content="Front End Developer, Web Application Developer, Web Developer, Javascript Developer"
            />
          </Helmet>
          <Banner text={this.state.text} />
          <BlogList blog={this.state.markdown} />
        </Main>
      );
    }
  }

  _mapFiles(context) {
    const keys = context.keys();
    const values = keys.map(context);
    return keys.reduce(
      (accumulator, key, index) => ({
        ...accumulator,
        [key]: values[index]
      }),
      {}
    );
  }

  _loadMarkdownData(markdownFiles) {
    for (const [index, file] of markdownFiles.entries()) {
      fetch(file)
        .then(response => {
          if (response.status === 200) {
            return response.text();
          }

          throw new Error(`Error: ${response.status}, ${response.statusText}`);
        })
        .then(text => {
          const mappedHeader = {
            date: Blog.objectToArray(text)[0],
            url: Blog.objectToArray(text)[1],
            title: Blog.objectToArray(text)[2],
            introduction: Blog.objectToArray(text)[3]
          };

          let mapTest = {
            id: index,
            html: Blog.transformMarkdownContent(text),
            header: [mappedHeader]
          };

          this.setState(prevState => ({
            markdown: [...prevState.markdown, mapTest]
          }));
        });
    }
  }
}

export default Blog;
